<template>
  <div class="item">
    <div>
      <div v-if="item.icon!==''" class="item-img"><img :alt="item.dbname" :src="item.icon" target="_blank" style="opacity: 1;">
      </div>
      <div v-else class="item-img"><img :alt="item.dbname" src="https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=2011412278,2173586856&fm=26&gp=0.jpg" target="_blank" style="opacity: 1;">
      </div>
      <h6>{{item.dbname}}</h6>
      <h3 >{{item.dbname}}</h3>
      <div class="item-btns clearfix">
        <span class="item-gray-btn"><router-link :to="{ name: 'item',query:{itemId:item.id}}" target="_blank">{{item.dbname}}</router-link></span>
        <span class="item-blue-btn" @click = "addCarPanelData(item)">问题上报</span>
      </div>
      <div class="item-price clearfix">
        <i>{{item.db_intro}}</i><span></span>
      </div>
      <div class="discount-icon">false</div>
      <div class="item-cover">
        <router-link :to="{ name: 'item',query:{itemId:item.id}}" target="_blank"></router-link>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "shopItem",
  props:{
    item:{
      type:Object
    }
  },
  data: () => ({
    cart:[] //控制选中的索引
  }),
  methods: {
    addCarPanelData(item){
                this.$message.success('问题上报成功，我们会尽快查看！');
    },
    }
}
</script>
<style>
</style>


