<template>
  <div id="main">
    <div class="sku-box store-content">
      <div class="warn" style="position:absolute;top:-35px;width:100%;z-index:20">
        <h1 v-if="showwarn" style="color:red;text-align:center;font-weight:bold;font-size:20px;">温馨提示：下载文献推荐使用<span style="color:green;">谷歌</span>、<span style="color:green;">搜狗</span>浏览器，请勿使用safari、QQ、IE等浏览器<a style="text-decoration:underline" href="javascript:;" @click="jmpTo"> >>点此下载chrome</a></h1>
        <h1 v-else style="color:red;text-align:center;font-weight:bold;font-size:20px;">温馨提示：.CAJ后缀文件请用CAJ阅读器打开<a style="text-decoration:underline" href="javascript:;" @click="jmpTo2"> >>>>点此下载CAJ阅读器</a></h1>
      </div>
              <div class="deng">
          <ma-Deng/>
          </div>
      <div class="sort-option">
          <div class="line clear">
        <ul class="line clear">
          <li><h1 style="color:black;font-weight: bold;font-size:20px">数据库类型:</h1></li>
          <li><a href="javascript:;" @click="sortByPrice(1)" :class="{active:sortType===1}">中文数据库</a></li>
          <li><a href="javascript:;" @click="sortByPrice(2)" :class="{active:sortType===2}">英文数据库</a></li>
          <li><a href="javascript:;" @click="sortByPrice(4)" :class="{active:sortType===4}">医学数据库</a></li>
          <li><a href="javascript:;" @click="sortByPrice(5)" :class="{active:sortType===5}">经济数据库</a></li>
          <li><a href="javascript:;" @click="sortByPrice(6)" :class="{active:sortType===6}">法律数据库</a></li>
          <li><a href="javascript:;" @click="sortByPrice(7)" :class="{active:sortType===7}">高校图书馆</a></li>
          <li><a href="javascript:;" @click="sortByPrice(3)" :class="{active:sortType===3}">常用工具</a></li>
          </ul>
          <div class="link-top">
            </div>
            <ul class="line clear">
              <li><h1 style="color:black;font-weight: bold;font-size:20px">资源类型:</h1></li>
            </ul>
         <ul class="line clear" v-if="sortType!=4">
        <li v-for="(item,index) in NavList.slice(0, 10)" :key="index" >
        <a href="javascript:;" @click="sort(item.id,item.intro,item.name)" :class="{active:sortType2===item.id}">{{item.name}}</a></li>
          </ul>
         <ul class="line clear" v-else>
        <li v-for="(item,index) in NavList.slice(0, 9)" :key="index">
        <a href="javascript:;" @click="sort(item.id,item.intro,item.name)" :class="{active:sortType2===item.id}">{{item.name}}</a></li>
          </ul>
         <ul class="line clear" v-if="NavList.length>=10&&sortType!=4">
        <li v-for="(item,index) in NavList.slice(10, 20)" :key="index">
        <a href="javascript:;" @click="sort(item.id,item.intro,item.name)" :class="{active:sortType2===item.id}">{{item.name}}</a></li>
          </ul>         
         <ul class="line clear" v-else>
        <li v-for="(item,index) in NavList.slice(9,16)" :key="index">
        <a href="javascript:;" @click="sort(item.id,item.intro,item.name)" :class="{active:sortType2===item.id}">{{item.name}}</a></li>
          </ul>
                   <ul class="line clear" v-if="NavList.length>=20&&sortType!=4">
        <li v-for="(item,index) in NavList.slice(20, 30)" :key="index">
        <a href="javascript:;" @click="sort(item.id,item.intro,item.name)" :class="{active:sortType2===item.id}">{{item.name}}</a></li>
          </ul>
                   <ul class="line clear" v-else>
        <li v-for="(item,index) in NavList.slice(16,26)" :key="index">
        <a href="javascript:;" @click="sort(item.id,item.intro,item.name)" :class="{active:sortType2===item.id}">{{item.name}}</a></li>
          </ul>
                             <ul class="line clear" v-if="NavList.length>=30&&sortType!=4">
        <li v-for="(item,index) in NavList.slice(30, 40)" :key="index">
        <a href="javascript:;" @click="sort(item.id,item.intro,item.name)" :class="{active:sortType2===item.id}">{{item.name}}</a></li>
          </ul>
                           <ul class="line clear" v-else>
        <li v-for="(item,index) in NavList.slice(26,50)" :key="index">
        <a href="javascript:;" @click="sort(item.id,item.intro,item.name)" :class="{active:sortType2===item.id}">{{item.name}}</a></li>
          </ul>
      </div>
      </div>
      <div class="gray-box">
        <div class="item-box">
          <shop-item v-for="item,index in goodsData" :item="item" :key="index"></shop-item>
        </div>
      </div>
    </div>
    <prompt tips="商品数量超过上限,无法继续添加！" v-show="maxOff"></prompt>
  </div>
 
</template>

<script>
import shopItem from '@/components/shopItem'
import prompt from '@/components/prompt'
import headerNav from '@/components/headerNav'
import maDeng from '@/components/maDeng'
import warnItem from '@/components/warn'
export default {
  name: "shop",
  data(){
    return {
    goodsData:[],
    goodsOriginData:[],
    sortType: 1,
    sortType2:1,
    NavList:[],
    goodsOriginNav:[],
    disabled: false,
    showwarn:true,
    }
  },
  components: {
    shopItem,
    prompt,
    headerNav,
    maDeng,
    warnItem,
  },
  computed:{
    maxOff(){//购物车单品数量最大限制
      return this.$store.state.maxOff
    }
  },
  mounted(){
  this.getGoodsData()
  this.getNavList()
  this.getUserInfo()
  const timer = setInterval(()=>{
    this.changeShow()
  },5000)
  this.$once('hook:beforeDestroy', ()=>{
    clearInterval(timer)
  })
  },
  methods:{
   compare( propertyName) {
     return  function( object1,  object2) {
       var value1  = object1[propertyName];
       var value2  = object2[propertyName];
       if (value2  > value1) {
         return  1;
      }  else  if (value2  < value1) {
         return  - 1;
      }  else {
         return  0;
      }
    }
  },
   async getGoodsData(){
    const {data:res}=await this.$http.get('http://lib.doc110.com/getDbList.php')
    this.goodsData=res
    this.goodsOriginData=this.goodsData
    this.goodsData  = this.goodsOriginData.filter ( item => item.dbtype==="1")
    },
    reset(){
      this.getGoodsData()
      this.sortType=1
    },
    sortByPrice (v) {
       if (v===1){    
    this.goodsData  = this.goodsOriginData.filter ( item => item.dbtype==="1")
    this.sortType=1
        this.NavList  = this.goodsOriginNav.filter ( item => item.type==="1")
          }else if(v===2){
            this.goodsData  = this.goodsOriginData.filter ( item => item.dbtype==="2")
            this.sortType=2
                this.NavList  = this.goodsOriginNav.filter ( item => item.type==="2")
          }else if(v===3){
 this.goodsData  = this.goodsOriginData.filter ( item => item.dbtype==="3")
            this.sortType=3
            this.NavList  = this.goodsOriginNav.filter ( item => item.type==="3")
          }else if(v===4){
 this.goodsData  = this.goodsOriginData.filter ( item => item.dbtype==="4")
 this.NavList  = this.goodsOriginNav.filter ( item => item.type==="4")
            this.sortType=4
          }else if(v===5){
 this.goodsData  = this.goodsOriginData.filter ( item => item.dbtype==="5")
            this.sortType=5
            this.NavList  = this.goodsOriginNav.filter ( item => item.type==="5")
          }else if(v===6){
 this.goodsData  = this.goodsOriginData.filter ( item => item.dbtype==="6")
            this.sortType=6
            this.NavList  = this.goodsOriginNav.filter ( item => item.type==="6")
          }else if(v===7){
 this.goodsData  = this.goodsOriginData.filter ( item => item.dbtype==="7")
            this.sortType=7
            this.NavList  = this.goodsOriginNav.filter ( item => item.type==="7")
          }
      },
         sort (v,m,n) {
      window.scrollTo(0,290);
    this.goodsData  = this.goodsOriginData.filter ( item => item.p_id===v)
    this.sortType2=v
    const h = this.$createElement;
    this.$notify({
          title: n+'简介',
          message: h('i', { style: 'color: blue'}, m),
          position: 'top-right',
          duration:2500,
          showClose:false,
        });     
         },
  async getNavList(){
    const {data:res}=await this.$http.get('http://lib.doc110.com/getNavList.php')
    console.log(res)
    res.sort(function (a, b) {
	if (a.weight < b.weight) {
		return -1;
	} else if (a.weight == b.weight) {
		return 0;
	} else {
		return 1;
	}
});
    this.NavList=res
    this.goodsOriginNav=this.NavList
    this.NavList  = this.goodsOriginNav.filter ( item => item.type==="1")
    },
    intro(v){
 this.$notify({
          title: '数据库介绍',
          message: h('i', { style: 'color: teal'},m),
        });

    },
    async getUserInfo(){
         const {data:res}=await this.$http.get('getUserInfo.php')
         if (res.code==200){
        window.sessionStorage.setItem('type', res.type)
        window.sessionStorage.setItem('expire', res.expiration)
        window.sessionStorage.setItem('cardtime', res.cardtime)
         window.sessionStorage.setItem('token', res.username)
         }else{
           this.$router.push('/login')
         }
    },
    jmpTo(){
      window.open("https://www.google.cn/chrome/", '_blank');
    },
        jmpTo2(){
      window.open("https://cajviewer.cnki.net/", '_blank');
    },
     changeShow(){
      if (this.showwarn==false){
        this.showwarn=true
      }else{
        this.showwarn=false
      }
  },
  }
}
</script>
<style>
  img{
    /*设置图片宽度和浏览器宽度一致*/
    width: 100%;
    height: inherit;
  }
  .sku-box{
      position: relative;
  }
  .sort-option{
      border-top: 1px solid #D8D8D8;
      color: #999;
      position: relative;
      top:20px;
  }
  .sort-option ul{
     
      line-height: 40px;
  }
  .sort-option li{
      position: relative;
      float: left;
      padding-left: 40px;
  }
  .sort-option li:first-child{
    padding-left: 9px;
  }
  .sort-option li:before{
    content: ' ';
      display: block;
      position: absolute;
      left: 20px;
      top: 50%;
      width: 2px;
      height: 2px;
      margin-top: -1px;
      background: #C7C7C7;
      padding:0;

  }
  .sort-option li:first-child:before{
    display: none;
  }
  .sort-option a{
    display: block;
      font-size: 18px;
      color: rgb(114, 114, 33);
      font-weight: bold;
  }
  
  .sort-option a.active, .sort-option a:hover{
      color: rgb(255, 255, 255);
      font-weight: bolder;
        background-color: #015c99cb;
  -webkit-box-shadow: 10px 10px 99px 6px #015c99cb;
  -moz-box-shadow: 10px 10px 99px 6px #015c99cb;
  box-shadow: 10px 10px 99px 6px #015c99cb;
      -webkit-border-radius: 5px 5px 5px 5px;
-moz-border-radius: 5px 5px 5px 5px;
border-radius: 5px 5px 5px 5px;
  }

.one {
  color: #4cc9f0;
}


.one:hover {
  background-color: #e6b01e;
  -webkit-box-shadow: 10px 10px 99px 6px #e6b01e;
  -moz-box-shadow: 10px 10px 99px 6px #e6b01e;
  box-shadow: 10px 10px 99px 6px #e6b01e;
}

.two:hover {
  background-color: #f038ff;
  -webkit-box-shadow: 10px 10px 99px 6px rgba(240, 56, 255, 1);
  -moz-box-shadow: 10px 10px 99px 6px rgba(240, 56, 255, 1);
  box-shadow: 10px 10px 99px 6px rgba(240, 56, 255, 1);
}

.three:hover {
  background-color: #b9e769;
  -webkit-box-shadow: 10px 10px 99px 6px rgba(185, 231, 105, 1);
  -moz-box-shadow: 10px 10px 99px 6px rgba(185, 231, 105, 1);
  box-shadow: 10px 10px 99px 6px rgba(185, 231, 105, 1);
}

  .gray-box{
    overflow: hidden;
      background: #fff;
      border-radius: 8px;
      border: 1px solid #dcdcdc;
      border-color: rgba(0,0,0,.14);
      box-shadow: 0 3px 8px -6px rgba(0,0,0,.1);
      position: relative;
      top:50px;
  }
  .sku-box .item-box{
    clear: both;
      overflow: hidden;
      margin: 0 -1px -1px -1px;
  }
  .sku-box .item{
      position: relative;
      float: left;
      border-right: 1px solid #efefef;
      border-bottom: 1px solid #efefef;
      width: 25%;
      height: 390px;
      background: #fff;
      box-sizing: border-box;
  }
  .sku-box .item:hover{
    box-shadow: 0 0 38px rgba(0,0,0,.08) inset;
      transition: all .15s ease;
  }
  .sku-box .item .item-img img{
    display: block;
      width: 200px;
      height: 180px;
      margin: 50px auto 10px;
  }
  .sku-box .item h3, .sku-box .item h6{
    overflow: hidden;
      text-align: center;
      text-overflow: ellipsis;
      white-space: nowrap;
  }
  
  .link-top {
            width: 100%;
            height: 1px;
            border-top: solid #ACC0D8 1px;
        }

  .sku-box .item h6{
      line-height: 1.2;
      font-size: 16px;
      color: #424242;
      margin: 0 auto;
      padding: 0 16px;
  }
  .sku-box .item h3{
    line-height: 1.2;
      font-size: 12px;
      color: #d0d0d0;
      margin: 8px auto 14px;
  }
  .sku-box .item .params-colors{
    margin-top: 23px;
      text-align: center;
  }
  .sku-box .item .colors-list{
    display: inline-block;
      overflow: hidden;
  }
  .sku-box .item .colors-list li{
    float: left;
      margin: 0 5px;
  }
  .sku-box .item .colors-list>li a{
    width: 8px;
      height: 8px;
      border: 1px solid #e5e5e5;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      border-radius: 50%;
      padding: 2px;
      display: block;
  }
  .sku-box .item .colors-list>li a.active{
      box-shadow: inset 0 0 0 1px #b2b2b2;
      border-color: #b2b2b2;
  }
  .sku-box .item .colors-list>li img{
      width: inherit;
      height: inherit;
      border-radius: 50%;
      display: block;
  }
  .sku-box .item .item-btns{
      position: absolute;
      left: 0;
      right: 0;
      bottom: 29px;
      text-align: center;
      opacity: 0;
      z-index: 10;
  }
  .sku-box .item:hover .item-btns{
      opacity: 1;
      transition: all .2s ease-in;
  }
  .sku-box .item .item-btns .item-blue-btn, .sku-box .item .item-btns .item-disabled-btn, .sku-box .item .item-btns .item-gray-btn, .sku-box .item .item-btns .item-green-btn{
    display: inline-block;
      box-sizing: border-box;
      width: 100px;
      height: 30px;
      font-size: 12px;
      line-height: 28px;
      border-radius: 4px;
      cursor: pointer;
      font-weight: 200;
      transition: all .1s ease;
  }
  .sku-box .item .item-btns .item-gray-btn{
    border: 1px solid #d5d5d5;
      color: #646464;
  }
  .sku-box .item .item-btns .item-gray-btn a{
      display: block;
      color: #a1a1a1;
  }
  .sku-box .item .item-btns .item-gray-btn:hover{
    background-image: linear-gradient(#f6f6f6,#ededed);
  }
  .sku-box .item .item-btns .item-blue-btn{
    background-color: #5c85e5;
      background-image: linear-gradient(#779ae9,#5078df);
      border: 1px solid #5c81e3;
      color: #fff;
      margin-left: 10px;
  }
  .sku-box .item .item-btns .item-blue-btn:hover{
    border: 1px solid #5374c8;
      background-color: #5074db;
      background-image: linear-gradient(#6e8ed5,#4769c2);
  }
  .sku-box .item .item-btns .item-blue-btn:active{
    border: 1px solid #3e61d7;
      background-color: #5c85e5;
      background-image: linear-gradient(#4d72de,#6189e6);
  }
  .sku-box .item .item-price{
    font-family: Arial;
      color: #c30a18;
      margin-top: 24px;
      text-align: center;
      opacity: 1;
  }
  .sku-box .item .item-price i{
      font-size: 16px;
  }
  .sku-box .item .item-price span{
    font-size: 18px;
      padding-left: 4px;
  }
  .sku-box .item:hover .item-price{
    opacity: 0;
      transition: all .1s ease-out;
  }
  .sku-box .item .discount-icon{
    display: none;
  }
  .sku-box .item .item-cover a{
    display: block;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 20;
      width: 100%;
      height: 310px;
  }
  .el-icon-question{
    position: relative;
    width:2px;
  }
</style>


