
<template>
  <div class="login_container">
    <div class="login_box">
      <div class="avatar_box">
        <img src="../assets/profile.png" alt="avatar" />
      </div>
      <div>
        <el-form
          ref="loginFormRef"
          :model="loginForm"
          :rules="loginFormRules"
          label-width="60px"
          class="login_form"
        >
          <el-form-item  prop="username" icon="el-icon-search">
            <el-input v-model="loginForm.username"
             prefix-icon="el-icon-s-custom"
             placeholder="输入卡号/账号">
             </el-input>
          </el-form-item>
          <el-form-item  prop="password">
            <el-input
              v-model="loginForm.password"
              type="password"
              prefix-icon="el-icon-unlock"
              placeholder="输入密码"
            ></el-input>
          </el-form-item>
          <el-form-item class="btns">
            <el-button type="primary" @click="login">马上登录</el-button>
            <el-button type="primary" @click="toshow">查看教程</el-button>
            <br>
            <br>
            <a style="color:red;font-size:15px;font-weight:bold;text-decoration:underline;position:absolute;left:50px;top:60px" href="http://ka.toyss.me/group/5.html" target="_blank"> 新老顾客续费/购买</a>
          </el-form-item>
        </el-form>
      </div>
    </div>
 
  </div>
</template>

<script>
export default {
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      // 表单验证
      loginFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入用户密码', trigger: 'blur' },
        ]
      }
    }
  },
  mounted(){
 this.show()
  },
  methods: {
    // 表单重置按钮
    toregister () {
      // console.log(this)
      // resetFields：element-ui提供的表单方法
      window.open("http://ka.toyss.me/group/5.html")
    },
    show(){
        if (!localStorage.getItem('isshow')){   
         this.$alert('<a style="color:red;font-size:17px;font-weight:bold;text-decoration:underline" href="https://www.jianshu.com/p/478c4920b569" target="_blank">知网下载教程</a></br></br></br><a style="color:red;font-size:17px;font-weight:bold;text-decoration:underline" href="https://www.jianshu.com/p/995a22b2aaeb" target="_blank">英文下载教程</a>', '必看教程', {
          dangerouslyUseHTMLString: true,
          customClass:"msgBox",
          confirmButtonText:"已阅",
          showCancelButton:true,
          center:true
        }).then(() => 
        localStorage.setItem('isshow',1)
        )
    }
    },
        toshow(){
         this.$alert('<a style="color:red;font-size:17px;font-weight:bold;text-decoration:underline" href="https://www.jianshu.com/p/478c4920b569" target="_blank">知网下载教程</a></br></br></br><a style="color:red;font-size:17px;font-weight:bold;text-decoration:underline" href="https://www.jianshu.com/p/995a22b2aaeb" target="_blank">英文下载教程</a>', '必看教程', {
          dangerouslyUseHTMLString: true,
          customClass:"msgBox",
          confirmButtonText:"已阅",
          showCancelButton:true,
          center:true
        }).then(() => 
        localStorage.setItem('isshow',1)
        )
    
    },
    login () {
      // 表单预验证
      // valid：bool类型
      this.$refs.loginFormRef.validate(async valid => {
        // console.log(valid)
        if (!valid) return false
        // this.$http.post('login', this.loginForm): 返回值为promise
        // 返回值为promise，可加await简化操作 相应的也要加async
        const { data: res } = await this.$http.post('http://lib.doc110.com/login.php', this.loginForm)
        // console.log(res)
        if (res.code !== 200) return this.$message({message:'登录失败',
        type:'warning'})
        this.$message(res.msg)
        window.sessionStorage.setItem('token', res.session)
        window.sessionStorage.setItem('type', res.type)
        window.sessionStorage.setItem('expire', res.expiration)
        window.sessionStorage.setItem('cardtime', res.cardtime)
        this.$router.push('/')
      })
    }
  }
}
</script>

<style lang="less">
/* // lang="less" 支持less格式
// scoped vue的指令，只在当前组件生效 */
.msgBox{
 width: 380px;
}
.login_container {
  position:absolute;
  background-color: #2b4b6b;
  height: 100%;
  width:100%;
  background: url(../assets/bg.jpg) no-repeat 50%;
	background-size: 100% 100%;
}
.login_box {
  position: absolute;
  width: 400px;
  flex-direction: row;
  height: 360px;
  background-color: #fff;
  border-radius: 3px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%,-50%);
  background: hsla(0,0%,100%,.3);


  .avatar_box {
    width: 130px;
    height: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}
.login_form {
  position: absolute;
  left:-30px;
  top:80px;
  bottom: 30px;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: center;
  position: relative;

  top:20px;
}
.info {
  font-size: 13px;
  
}

.el-message--error{
  background-color:#fdf6ec;
  border-color: #fdf6ec;
}
.el-message .el-icon-error,.el-message--error .el-message__content{
  color:#E6A23C
}
.el-icon-error:before{
  content: "\E7A3";
}
</style>



