<template>
  <div class="block" style="border-radius：30px；">
    <el-carousel height="250px">
      <el-carousel-item v-for="(item,index) in AdList" :key="index">
       <!-- <h3 class="small">{{ item }}</h3>-->
       <img :src="item.imageurl">
      </el-carousel-item>
    </el-carousel>
  </div>
</template>
<script>
export default {
    data(){
     return{
      AdList:[],
}
    },
    methods:{
     async getAdList(){
            const { data: res } = await this.$http.get('http://lib.doc110.com/getAdList.php')
            this.AdList=res;
        }
    },
    mounted(){
        this.getAdList()
    }
}
</script>>
<style>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 35px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
    text-align: center;
  }
  .el-carousel__item:nth-child(2n) {
     background-color: #b6c6d8;
     
  }
  
  .el-carousel__item:nth-child(2n+1) {
     background-color: #b6c6d8;
  }
</style>


