import Vue from 'vue'
import Router from 'vue-router'
import login from '@/views/login'
import register from '@/views/register'
import shop from '@/views/shop'
import item from '@/views/item'
import shopList from '@/views/shoplist'
import checkOut from '@/views/checkout'
import payment from '@/views/payment'
import account from '@/views/account'
import order from '@/views/account/order'
import ads from '@/views/account/ads'
import '@/assets/css/app.css'

Vue.use(Router)

const router=new Router({
  mode:'hash',
  routes: [
    {
      path:'/',
      name:'shop',
      component:shop,
      meta: {
        keepAlive: true
      }
    },
    {
      path:'/item',
      name:'item',
      component:item,
      meta: {
        keepAlive: true
      }
    },
    {
      path:'/shopList',
      name:'shopList',
      component:shopList,
      meta: {
        keepAlive: false
      },
    },  
    {
      path:'/checkOut',
      name:'checkout',
      component:checkOut
    },
    {
      path:'/login',
      name:'login',
      component:login,
      meta: {
        keepAlive: false
      }
    },
    {
      path:'/register',
      name:'register',
      component:register,
      meta: {
        keepAlive: false
      }
    },
    {
      path:'/payment',
      name:'payment',
      component:payment
    },
    {
      path:'/account',
      component:account,
      children:[
        {
          path:'',
          name:'account',
          component:order,
          meta: {
            keepAlive: true
          },
        },
        {
          path:'address',
          name:'ads',
          component:ads,
          meta: {
            keepAlive: true
          },
        }
      ]
    },
    {
      path:'*',
      redirect:'/'
    }
  ]
})

router.beforeEach((to, from, next) => {
  // 访问登录页，放行
  if (to.path === '/login') return next()
  if (to.path === '/') return next()
  if (to.path === '/register') return next()
  next()
})
export default router;


