<template>
  <div id="header">
    <div class="nav-global" :class="navBarFixed == true ? 'navBarWrap' :''">
      <div class="container">
        <h1 class="nav-logo">
          <a href="javascript:;"></a>
        </h1>
        <ul class="nav-aside">
          <li class="nav-user">
          <div v-if="expire">
          <a style="position: absolute; left:-380px;color :#997;font-weight:bolder">权限 :{{type}}</a>
          <a style="position: absolute; left:-230px;color :#997;font-weight:bolder">期限 :{{cardtime}}</a>
          </div>
            <a href="javascript:;">个人中心</a>
            <!--active-->
            <div class="nav-user-wrapper">
              <div class="nav-user-list">
                <dl class="nav-user-avatar">
                  <dd><span class="ng-scope"></span></dd>
                  <dt class="ng-binding">{{token}}</dt>
                </dl>
                <ul v-if="!token==''">
                  <router-link class="order" tag="li" :to="{ name: 'account'}">
                    <a>{{type}}</a>
                  </router-link>
                  <li class="order">
                    <a>有效至{{expire.split(' ')[0]}}</a>
                  </li>
                    <li class="logout">
                      <a href="javascript:;" @click="logout">退出</a>
                      </li>
                </ul>
                  <ul v-else>
                  <router-link class="order" tag="li" :to="{ name: 'login'}">
                    <a>尚未登陆</a>
                  </router-link>
                </ul>
              </div>
            </div>
          </li>
          <!--active-->
          <!--active-->
        </ul>
        <ul class="nav-list">
        <li v-for="(item,index) in Navlist">
        <a :href="item.url" target="_blank">{{item.name}}</a></li>
        </ul>
      </div>
    </div>
    <div class="nav-sub">
      <div class="nav-sub-wrapper">
        <div class="container">
          <ul class="nav-list">
            <router-link tag="li" :to="{ name: 'shop'}"><a>首页</a></router-link>
            <router-link tag="li" class="active":to="{ name: 'shop'}"><a>在线图书馆</a></router-link>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import carPanel from '@/components/carPanel'
export default {
  name: 'headerNav',
  data () {
    return {
      expire:'1970-00-00 00:00',
      token:'',
      type:'未登录',
      Navlist:[],
      cardtime:'',
      navBarFixed: false,
    }
  },
  components: {
    carPanel
  },
  mounted(){
   this.getinfo()
   this.getNav()
   window.addEventListener("scroll", this.watchScroll);
  },
  destroyed() {
       window.removeEventListener("scroll", this.watchScroll);
        },
  methods:{
   getinfo(){
      this.expire = window.sessionStorage.getItem('expire'),
      this.token=window.sessionStorage.getItem('token'),
      this.type=window.sessionStorage.getItem('type')
      this.cardtime=window.sessionStorage.getItem('cardtime')
      if (parseInt(this.cardtime)>=366){
        this.cardtime='永久权限'
      }
      else{
        this.cardtime=this.cardtime+'天卡'
      }
      if (this.type==='1'){
        this.type='中文会员'
      }
      else if(this.type==='2'){
        this.type='英文会员'
      }else if (this.type==='3'){
         this.type='中英文会员'
      }else{
        this.type='未登录'
      }
   },
   logout(){
      window.sessionStorage.clear()
      this.$http.get('http://lib.doc110.com/logout.php')
      this.$message.success('已退出登陆！')
      this.$router.push('/login')
   },
   async getNav(){
    const {data:res}=await this.$http.get('http://lib.doc110.com/getNav.php')
    this.Navlist=res
   },
                 watchScroll () {
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        //  当滚动超过 50 时，实现吸顶效果
        if (scrollTop > 49) {
          this.navBarFixed = true
        } else {
          this.navBarFixed = false
        }
     }
  }
}
</script>

<style scope>
.navBarWrap {
    position:fixed;
    top:0;
    z-index:1200;
    width:100%
  }
</style>



